.App {
  text-align: center;
}
td,th{
  border: 2px solid black;
}
caption{
  font-size: 2.5rem;
  font-weight:bold;
  font-family: cursive;
  background-color: lightgrey;
}
table{
  border: 2px solid black;
  border-collapse: collapse;
  width: 900px;
  height : 500px;
  background-color: aqua;
}
tbody{
  font-size: 1.5rem;
  height: 300px;
}
tfoot{
  font-size: 1.5rem;
  height: 50px;
  background-color: bisque;
}
thead{
  font-size: 1.5rem;
  height: 100px;
  background-color: aquamarine;
}

